.loader {
  width: 200px;
  height: 200px;
  background-color: rgba(0, 0, 0, 0);
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 200px;
  height: 200px;
  animation: animloader 1s linear infinite;
  animation-delay: 0, 2s;
  border: 4px solid red;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animloader {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

